import "./Boost.scss";

import Tabs from "../Common/Tabs/Tabs";
import Task from "../Common/Task/Task";
import TextHeader from "../Common/TextHeader/TextHeader";

import tipImg from "../../img/svgicons/tip.svg";
import { useSelector } from "react-redux";

export default function Boost() {
  const tasks = useSelector((state) => state.tasks);

  const tabMoreClick = [
    <Task
      title="-"
      tasks={[
        {
          task_id: 9999,
          icon: "star",
          text: "-",
          award_text: "  ,   ",
          is_ad: true,
          progress: {
            is_done: false,
          },
        },
      ]}
    />,
    <Task tasks={tasks.moreClicks} />,
  ];
  const tabPartnersTgGames = [<Task tasks={tasks.taskTgPartners} />];
  const tabGoogleTask = [
    <div className="block-with-bg">
      <Task title="  " />
      <div className="count-block">
        <p>100 000</p>
        <p></p>
      </div>

      <p>
           ,     ,  
        ,     .
      </p>

      <div className="tip">
        <img src={tipImg} alt="tipImg" />
        <p>
          <b>   !</b>    ,  
                .
        </p>
      </div>
    </div>,
    <Task
      tasks={[
        {
          taskType: "google",
          taskTitle: "     Google",
          taskText: "+10 000 000 ",
          isNew: true,
        },
        {
          taskType: "google",
          taskTitle: "      Google",
          taskText: "+100 000 ",
        },
        {
          taskType: "star",
          taskTitle: "  Candy Defense -  $DEFENSE  ",
          taskText: "+30 000 ",
        },
      ]}
    />,
  ];
  const tabsLinks = [
    "  ",
    "    ",
    // "Google ",
    // "  ",
  ];
  const tabsContent = [
    tabMoreClick,
    tabPartnersTgGames,
    // tabGoogleTask,
    // "  ",
  ];
  return (
    <section className="block-white">
      <TextHeader
        title={[
          <>
            <span>  </span> -   ,  
            TON.
          </>,
        ]}
        subTitle={"     TON    !"}
      />
      <Tabs tabsLinks={tabsLinks} tabsContent={tabsContent} />
    </section>
  );
}
