import { useSelector } from "react-redux";

export default function BtnInvite({ parentClassName = "", children }) {
  const refferralLink = useSelector(
    (state) => state.userStats.user.refferralLink
  );

  const handleShare = async () => {
    const url = `https://t.me/share/url?url=${encodeURIComponent(
      refferralLink
    )}&text=${encodeURIComponent(
      `\n       -,     TON! \n    ,    ,           !\n        TON! `
    )}`;
    window.open(url, "_blank");
  };
  return (
    <button onClick={handleShare} className={parentClassName}>
      {children}
    </button>
  );
}
